.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.card{
  width: 90%;
  background-color: transparent;
  display:flex;
  flex-direction: row;
  justify-content: space-evenly;
  padding: 1rem;
  flex-flow: wrap;
  
}
.card_container{
  /* background-color: red; */
  justify-content: center;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.card-content{
  width:30%;
  /* border: solid 1px black; */
  margin: 1rem 0rem;
  /* border-left: 3px solid black; */
  box-shadow: 0px 1px 5px 1px #e0e0e0;
}
.card-content:first-child{
  width:95%;
  border-top:solid 5px #f44336;
}

.card-content:nth-child(2){
  /* background-color: #00e676; */
  border-left: solid 5px #00e676;
}
.card-content:nth-child(3){
  /* background-color: #ef5350; */
  border-left: solid 5px #ef5350;

}
.card-content:nth-child(4){
  /* background-color: #e3f2fd ; */
  border-left: solid 5px #1976d2;
}
.card-content:nth-child(5){
  /* background-color: #ef5350; */
  border-left: solid 5px #00e676;

}
.card-content:nth-child(6){
  /* background-color: #ef5350; */
  border-left: solid 5px #ef5350;

}
.card-content:nth-child(7){
  /* background-color: #e3f2fd; */
  border-left: solid 5px #1976d2;
}


.card-typography1,.card-typography2{
  border-bottom: solid 1px #e0e0e0;
  /* color: white; */
  /* text-align: center; */
}
.card-typography2{
  font-size: 30px;
  text-align: left;
  padding: 1rem 0rem;
}

.card-typography1,.card-typography2,.card-typography3{
  word-wrap: break-word;
}

/*  */
.searchCon{
  background-color:inherit;
  width: 90%;
  padding: 1rem;
  display: flex;
  justify-content: space-between;
  box-shadow: 0px 1px 5px 1px #e0e0e0;
  margin: 1rem;
}
.form-control{
  width: 100%;
}
.input-base{
  border-bottom: solid 1px #888888;
  width: 100%;
  padding: 10px;
}

.country-card{
  box-shadow: 0px 1px 5px 1px #e0e0e0;
}
/*  */
.detail-container{
  width: 90%;
  display: flex;
  flex-direction: column;
  /* background-color: red; */
  margin:1rem auto;
  padding: 1rem;
  box-shadow: 0px 1px 5px 1px #e0e0e0;

}

@media only screen and (max-width: 600px) {
  .card-content {
    width: 100%;
  }
  .card{
    width: 100%;
  }
  .searchCon{
    width: 100%;
  }
  .detail-container{
    width: 100%;
  }
}